import React, {} from 'react';

const WorkList = ({site, active, currentRef, onMouseMove}) => {
  const {link, thum, year, title, description, tools, copy} = site;

  const url = (e) => {
    if(e === ''){
      return alert('오픈 준비중입니다');
    }
    window.open(e);
  }

  return (
    <li className={`${active ? "active" : ""}`} ref={active ? currentRef : null}>
      <a href onClick={() => url(link)} rel="noopener noreferrer">
        <figure>
          <img src={thum} alt="" />
        </figure>
        <dl>
          <dt>
            <i>{year}</i>
            {title}
          </dt>
          <dd className="text">
            {description.map((text, i) => (
              <span key={i}>{text}</span>
            ))}
          </dd>
          <dd className="tools">
            {tools}
          </dd>
        </dl>
      </a>
      <span className="copy">{copy}</span>
    </li>
  )
};
export default WorkList;